<template>
  <div class="menu container py-5">
    <!-- Badge -->
    <div class="alert alert-info text-center py-2 mb-4">{{ estado }}</div>

    <!-- Encabezado -->
    <header class="text-center mb-4" style="background:linear-gradient(90deg,#ff512f,#f09819);padding:16px;border-radius:12px;">
      <img :src="base + 'img/escudo.png'" alt="Pollos Dumbo" style="height:90px;width:auto" />
      <h1 class="mt-2 brand">Pollos Dumbo</h1>
      <div class="d-flex justify-content-center gap-3 flex-wrap">
        <RouterLink :to="{name:'home'}" class="btn btn-light btn-lg fw-bold px-4">Inicio</RouterLink>
        <RouterLink :to="{name:'pedido'}" class="btn btn-dark btn-lg fw-bold px-4">Hacer Pedido</RouterLink>
      </div>
    </header>

    <!-- Comidas -->
    <h2 class="text-center mb-4 brand">Nuestro Menú</h2>
    <div class="row g-4">
      <div v-for="p in productos" :key="p.key" class="col-sm-6 col-lg-4">
        <div class="card h-100">
          <img :src="base + p.img" class="card-img-top" :alt="p.nombre" />
          <div class="card-body d-flex flex-column">
            <h5 class="card-title">{{ p.nombre }}</h5>
            <p class="text-muted">{{ p.desc }}</p>
            <div class="mt-auto d-flex justify-content-between align-items-center">
              <span class="fw-bold">Bs {{ p.precio.toFixed(2) }}</span>
              <RouterLink :to="{name:'pedido', query:{ producto:p.key }}" class="btn btn-sm btn-brand">Pedir</RouterLink>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Bebidas -->
    <h2 class="text-center mt-5 mb-4 brand">Bebidas Gaseosas</h2>
    <div class="row g-4">
      <div v-for="b in bebidasCards" :key="b.key" class="col-6 col-lg-4">
        <div class="card h-100">
          <img :src="base + b.img" class="card-img-top drink-img" :alt="b.nombreCompleto" />
          <div class="card-body d-flex flex-column">
            <h5 class="card-title">{{ b.nombreCompleto }}</h5>
            <p class="text-muted">Refresco gaseoso</p>
            <div class="mt-auto d-flex justify-content-between align-items-center">
              <span class="fw-bold">Bs {{ b.precio.toFixed(2) }}</span>
              <RouterLink :to="{name:'pedido', query:{ refresco:'gaseosa', marca:b.marca, tam:b.tamano }}" class="btn btn-sm btn-outline-success">Pedir</RouterLink>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import CATALOGO from "@/data/catalogo";

export default {
  name: "PolloView",
  data(){
    return{
      base: process.env.BASE_URL,
      estado: "Cargando horario…",
      productos: [
        { key:'entero', nombre:'Pollo Entero Frito', desc:'Ideal para compartir.', img:'img/entero.PNG', precio: CATALOGO.pollo.entero },
        { key:'broaster', nombre:'Pollo Broaster', desc:'Crujiente y sabroso.', img:'img/broaster.jpg', precio: CATALOGO.pollo.broaster },
        { key:'frito', nombre:'Pollo Frito Tradicional', desc:'Receta clásica de la casa.', img:'img/spiedo.jpg', precio: CATALOGO.pollo.frito },
        { key:'arroz', nombre:'Porción de Arroz', desc:'Arroz blanco.', img:'img/arroz.PNG', precio: CATALOGO.porciones.arroz },
        { key:'papa', nombre:'Papa Frita', desc:'Porción de papas.', img:'img/papita.png', precio: CATALOGO.porciones.papa },
        { key:'fideo', nombre:'Porción de Fideo', desc:'Fideos al dente.', img:'img/fideo.PNG', precio: CATALOGO.porciones.fideo },
      ],
    };
  },
  computed:{
    bebidasCards(){
      // 3 marcas × 3 tamaños = 9 cards
      const imgs = {
        coca:   { mini:'img/coca_popular.png',  popular:'img/coca_popular.png',  mediana:'img/coca_popular.png' },
        fanta:  { mini:'img/fanta_mini.jpg',    popular:'img/fanta_flaca.webp',  mediana:'img/fanta_flaca.webp' },
        sprite: { mini:'img/sprite_mini.jpg',   popular:'img/sprite_popular.webp', mediana:'img/sprite_flaca.webp' },
      };
      const nombres = { coca:'Coca-Cola', fanta:'Fanta', sprite:'Sprite' };
      const tamNom  = { mini:'Mini', popular:'Popular', mediana:'Mediana' };

      const cards = [];
      for (const marca of Object.keys(CATALOGO.bebidas.gaseosa)){
        for (const tam of Object.keys(CATALOGO.bebidas.gaseosa[marca])){
          cards.push({
            key: `${marca}-${tam}`,
            marca,
            tamano: tam,
            nombreCompleto: `${nombres[marca]} ${tamNom[tam]}`,
            precio: CATALOGO.bebidas.gaseosa[marca][tam],
            img: imgs[marca][tam]
          });
        }
      }
      return cards;
    }
  },
  mounted(){
    // Badge abierto/cerrado
    const horario = { open:'15:00', close:'21:00' };
    const toMin = s => { const [h,m]=s.split(':').map(Number); return h*60+m; };
    const now = new Date(), mins = now.getHours()*60 + now.getMinutes();
    const abierto = mins >= toMin(horario.open) && mins <= toMin(horario.close);
    this.estado = `${abierto ? 'ABIERTO':'CERRADO'} • ${horario.open}–${horario.close} (Llallagua)`;
  }
};
</script>
