<template>
  <div class="container-fluid g-0">
    <div class="row g-0">
      <!-- Lado izquierdo -->
      <aside class="col-lg-4 col-xl-3 side">
        <img :src="base + 'img/escudo.png'" alt="Pollos Dumbo" class="brand-logo" />
        <div class="brand-title">Pollos Dumbo</div>

        <div class="d-flex flex-column gap-2 w-100" style="max-width:320px;">
          <RouterLink :to="{name:'home'}" class="btn btn-light text-dark w-100">Inicio</RouterLink>
          <RouterLink :to="{name:'pollo'}" class="btn btn-dark w-100">Ver Menú</RouterLink>
        </div>

        <div class="mt-4 text-center" style="opacity:.9;">
          <small>Atendemos de <b>15:00</b> a <b>21:00</b><br/> Llallagua</small>
        </div>
      </aside>

      <!-- Lado derecho -->
      <section class="col-lg-8 col-xl-9 p-3">
        <div class="card mx-auto" style="max-width:560px;">
          <div class="card-header">Pedido de Pollos Dumbo</div>
          <div class="card-body">
            <form @submit.prevent="enviarPedido">
              <!-- Producto -->
              <div class="mb-3">
                <label class="form-label">Producto</label>
                <select v-model="form.producto" class="form-select" required>
                  <option value="">Seleccione...</option>
                  <option value="entero">Pollo Entero</option>
                  <option value="broaster">Pollo Broaster</option>
                  <option value="frito">Pollo Frito Tradicional</option>
                </select>
              </div>

              <!-- Porción -->
              <div class="mb-3">
                <label class="form-label">Porción</label>
                <select v-model="form.porcion" class="form-select">
                  <option value="">Ninguna</option>
                  <option value="arroz">Arroz</option>
                  <option value="papa">Papa frita</option>
                  <option value="fideo">Fideo</option>
                  <option value="salsa">Salsa</option>
                </select>
              </div>

              <!-- Refresco -->
              <div class="mb-3">
                <label class="form-label">Refresco</label>
                <select v-model="form.refresco" class="form-select">
                  <option value="">Ninguno</option>
                  <option value="maracuya">Refresco de maracuyá</option>
                  <option value="limonada">Limonada</option>
                  <option value="gaseosa">Gaseosa (elige marca y tamaño)</option>
                </select>
              </div>

              <div v-if="form.refresco==='gaseosa'" class="row g-2">
                <div class="col-sm-6">
                  <label class="form-label">Marca</label>
                  <select v-model="form.gaseosaMarca" class="form-select">
                    <option value="coca">Coca-Cola</option>
                    <option value="fanta">Fanta</option>
                    <option value="sprite">Sprite</option>
                  </select>
                </div>
                <div class="col-sm-6">
                  <label class="form-label">Tamaño</label>
                  <select v-model="form.gaseosaTam" class="form-select">
                    <option value="mini">Mini</option>
                    <option value="popular">Popular</option>
                    <option value="mediana">Mediana</option>
                  </select>
                </div>
              </div>

              <!-- Pago -->
              <div class="mb-3 mt-2">
                <label class="form-label">Método de pago</label>
                <div class="list-group">
                  <label class="list-group-item">
                    <input class="form-check-input me-2" type="radio" value="caja" v-model="form.pago"> Pagar en caja (efectivo)
                  </label>
                  <label class="list-group-item">
                    <input class="form-check-input me-2" type="radio" value="qr" v-model="form.pago"> QR (Billetera/APP)
                  </label>
                </div>
              </div>

              <!-- Panel QR -->
              <div v-if="form.pago==='qr'" class="text-center">
                <strong>Pago por QR</strong>
                <img :src="base + 'img/QR.jpg'" alt="QR" class="qr-img" />
              </div>

              <!-- Total -->
              <div class="total-box mt-3">
                <span>Total estimado</span>
                <span>Bs {{ total.toFixed(2) }}</span>
              </div>

              <div class="d-grid gap-2 mt-3">
                <button type="submit" class="btn btn-brand btn-lg">Realizar pedido</button>
                <button type="button" @click="enviarWhatsApp" class="btn btn-success btn-lg">Enviar por WhatsApp</button>
              </div>
            </form>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import CATALOGO from "@/data/catalogo";

export default {
  name: "PedidoView",
  data(){
    return{
      base: process.env.BASE_URL,
      form:{
        producto: this.$route.query.producto || "",
        porcion: "",
        refresco: this.$route.query.refresco || "",
        gaseosaMarca: this.$route.query.marca || "coca",
        gaseosaTam: this.$route.query.tam || "popular",
        pago: "caja"
      }
    };
  },
  computed:{
    total(){
      const { pollo, porciones, bebidas } = CATALOGO;
      let t = 0;
      if(this.form.producto) t += pollo[this.form.producto] || 0;
      if(this.form.porcion) t += porciones[this.form.porcion] || 0;
      if(this.form.refresco === "gaseosa"){
        t += bebidas.gaseosa[this.form.gaseosaMarca]?.[this.form.gaseosaTam] || 0;
      }else if(this.form.refresco){
        t += bebidas[this.form.refresco] || 0;
      }
      return t;
    }
  },
  methods:{
    enviarPedido(){ alert("Pedido enviado (demo)"); },
    enviarWhatsApp(){
      const numero = "59174148930";
      const texto = `Pedido: ${this.form.producto||'-'} | Porción: ${this.form.porcion||'-'} | Refresco: ${this.form.refresco||'-'} | Total: Bs ${this.total.toFixed(2)}`;
      window.open(`https://wa.me/${numero}?text=${encodeURIComponent(texto)}`,'_blank');
    }
  }
};
</script>
